.Clock {
    width: 500px;
    height: 500px;
    position: relative;
    scale: 0.4;
}

.Clock-Background {
    width: 500px;
    height: 500px;
    border-radius: 50%;
    z-index: 3;
    position: relative;
}

.Clock-Arm-Container {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 500px;
    height: 500px;
}

.Clock-Right-Arm {
    position: absolute;
    /* bottom: 200px; */
    left: 45%;
    
    z-index: 4;

}

.Clock-Left-Arm {
    scale: 0.8;
    position: absolute;
    left: 25%;
    /* bottom: 200px; */
    z-index: 5;

}

.Clock-Hashes {
    position: absolute;
    top: -25px;
    left: -25px;
    z-index: 1;
}
/* 570x610 */
.Digital-Clock {
    position: relative;
    bottom: -20px;
}

.outer_face {
    width: 550px; /* width of clock */
    height: 550px; /* height of clock */
    border-radius: 50%; /* clock round corner radius */
    background: transparent;
    z-index: 1;

}

.outer_face::before, .outer_face::after, .outer_face .marker { /* time markers syle */
    content: "";
    position: absolute;
    width: 10px; /* width of 12-6 and 3-9 markers */
    height: 100%;
    background: white;
    z-index: 1;
    left: 50%;
    margin-left: -5px; /* set this value of 1/2 marker width */
    top: 0;
    }

.outer_face::after {
    -moz-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg)
    }

.outer_face .marker {
    background: white;
    width: 6px; /* width of all other markers */
    margin-left: -3px /* set this value of 1/2 marker width */
    }

.outer_face .marker.oneseven {
    -moz-transform: rotate(30deg);
    -ms-transform: rotate(30deg);
    -webkit-transform: rotate(30deg);
    transform: rotate(30deg)
    }

.outer_face .marker.twoeight {
    -moz-transform: rotate(60deg);
    -ms-transform: rotate(60deg);
    -webkit-transform: rotate(60deg);
    transform: rotate(60deg)
    }

.outer_face .marker.fourten {
    -moz-transform: rotate(120deg);
    -ms-transform: rotate(120deg);
    -webkit-transform: rotate(120deg);
    transform: rotate(120deg)
    }

.outer_face .marker.fiveeleven {
    -moz-transform: rotate(150deg);
    -ms-transform: rotate(150deg);
    -webkit-transform: rotate(150deg);
    transform: rotate(150deg)
    }

.inner_face {
    position: relative;
    width: 88%;
    height: 88%;
    background: transparent;
    -moz-border-radius: 1000px;
    -webkit-border-radius: 1000px;
    border-radius: 1000px;
    z-index: 1000;
    left: 6%; /* set this value of 1/2 width value*/
    top: 6% /* set this value of 1/2 height value*/
    }
